:root {
  --font-body: "Space Mono", monospace;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --strongcyan: hsl(172, 67%, 45%);
  --verydarkcyan: hsl(183, 100%, 15%);
  --darkgrayishcyanactive: hsl(186, 14%, 43%);
  --darkgrayishcyan: hsl(184, 14%, 56%);
  --lightgrayishcyan: hsl(189, 41%, 97%);
  --lightgrayishcyanactive: hsl(185, 41%, 84%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 24px;
  background-color: var(--lightgrayishcyanactive);
  font-family: "Space Mono", monospace;
}

input,
textarea,
select {
  font-family: inherit;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.navbar {
  display: block;
  margin: 8rem auto 4rem auto;
  padding: 0;
  text-align: center;
}

form {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.calc .calc-container {
  display: flex;
  margin: auto;
  width: 800px;
  height: 25rem;
  background-color: var(--white);
  border-radius: 25px;
}
.calc .calc-container .calc-inputs {
  display: grid;
  width: 50%;
  margin: 2rem;
  margin-right: 1rem;
}
.calc .calc-container .calc-inputs input[type=number] {
  -moz-appearance: textfield;
  padding: 0.5rem;
  background-color: var(--lightgrayishcyan);
  border: none;
  border-radius: 5px;
  color: var(--verydarkcyan);
  font-size: 1.2rem;
  font-weight: 700;
  text-align: right;
  outline: none;
}
.calc .calc-container .calc-inputs label {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
  font-weight: 700;
}
.calc .calc-container .calc-inputs .dollar {
  position: relative;
  top: -28px;
  left: 15px;
}
.calc .calc-container .calc-inputs .calc-tip-title {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
}
.calc .calc-container .calc-inputs .calc-percs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.calc .calc-container .calc-inputs .calc-percs input[type=radio] {
  position: absolute;
  top: 0;
  left: -10px;
  width: 1rem;
  height: 1rem;
  padding: 0;
  margin: 0;
}
.calc .calc-container .calc-inputs .calc-percs .option-label {
  position: relative;
  background-color: var(--verydarkcyan);
  color: var(--white);
  font-weight: 700;
  border-radius: 5px;
  padding: 0;
  margin: 0;
  cursor: pointer;
}
.calc .calc-container .calc-inputs .calc-percs .option-label:hover {
  background-color: var(--lightgrayishcyanactive);
  color: var(--verydarkcyan);
}
.calc .calc-container .calc-inputs .calc-percs .option-label.active {
  background-color: var(--strongcyan);
  color: var(--verydarkcyan);
}
.calc .calc-container .calc-inputs .calc-percs h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  padding: 1rem 2rem;
}
.calc .calc-container .calc-inputs .calc-percs #option-custom-label {
  position: relative;
  top: 0;
  left: 0;
}
.calc .calc-container .calc-inputs .calc-percs #option-custom-label input[type=name] {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  font-size: 1.5rem;
  font-weight: 700;
  background-color: var(--lightgrayishcyanactive);
  color: var(--verydarkcyan);
  text-align: center;
  border: none;
  border-radius: 5px;
}
.calc .calc-container .calc-inputs .calc-percs #option-custom-label [type=radio] {
  position: absolute;
  top: 0;
  left: 0px;
  opacity: 0;
  z-index: -1;
}
.calc .calc-container .calc-inputs .people-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}
.calc .calc-container .calc-inputs .people-wrapper .calc-number-of-people {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
  width: fit-content;
  font-weight: 700;
}
.calc .calc-container .calc-inputs .people-wrapper .zero-people {
  display: flex;
  font-size: 0.8rem;
  width: fit-content;
  color: red;
  opacity: 0;
}
.calc .calc-container .calc-inputs .people-icon {
  position: relative;
  left: 15px;
  top: -28px;
}
.calc .calc-container .calc-visor {
  display: grid;
  width: 50%;
  margin: 2rem;
  margin-left: 1rem;
  padding: 2rem;
  border: 1px solid green;
  background-color: var(--verydarkcyan);
  border-radius: 25px;
  border: none;
}
.calc .calc-container .calc-visor .calc-tip-amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calc .calc-container .calc-visor .calc-tip-amount h1 {
  margin: 0;
  font-size: 2.5rem;
  color: var(--strongcyan);
}
.calc .calc-container .calc-visor .calc-tip-amount-title {
  font-size: 0.8rem;
  color: var(--white);
  margin: 0;
  width: 90px;
}
.calc .calc-container .calc-visor .calc-tip-amount-person {
  font-size: 0.7rem;
  color: var(--darkgrayishcyan);
}
.calc .calc-container .calc-visor .calc-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calc .calc-container .calc-visor .calc-total h1 {
  margin: 0;
  font-size: 2.5rem;
  color: var(--strongcyan);
}
.calc .calc-container .calc-visor .calc-total-title {
  font-size: 0.8rem;
  color: var(--white);
  margin: 0;
  width: 90px;
}
.calc .calc-container .calc-visor .calc-total-person {
  font-size: 0.7rem;
  color: var(--darkgrayishcyan);
}
.calc .calc-container .calc-visor button {
  position: relative;
  top: 47px;
  left: 0;
  height: 2.5rem;
  border: none;
  border-radius: 5px;
  background-color: var(--darkgrayishcyan);
  opacity: 0.3;
  color: var(--verydarkcyan);
  font-weight: 900;
  font-size: 1rem;
  letter-spacing: 0.2mm;
  cursor: pointer;
}

footer {
  text-align: center;
  font-size: 0.8rem;
  margin: 1rem;
}